				"html": "<p>右脳と左脳を切り離されると連絡ができなくなり<br>\nそれぞれ別の判断と行動がなされる。<br>\n左脳は論理的に考えて行動しようとして右手に命令する。<br>\n右脳は感情的に考えて行動しようとして左手に命令する。<br>\n論理と感情が相反する事象に対応しようとしたとき<br>\n右手と左手は相反する行動をしようとし<br>\nしかもそうであることを自覚することもできない。  </p>\n<p>逆にいうと，通常我々は自分の中の相反する判断に対して<br>\n無意識に折り合いをつけながら生きていると言える。<br>\n矛盾した自分というのは当たり前で<br>\n自分というのははじめから１つではなく<br>\n様々な感情や意志など複数の要素が関連し合い<br>\n統合されている（と思っている）状態である。  </p>\n<p>脳の機能のうち<br>\n自分を自分として認識している部分が破損した人は<br>\n自分を固定されている存在ではなく<br>\n水のように流動的で境界のないもののように感じる。<br>\nそして，感覚は世界へと延長され<br>\n宇宙と一体となったかのようにすら感じる。  </p>\n<p>実際，生命はその構成要素を刻々と置き換えながら<br>\nある種の流れの中に生きている。<br>\n霊というの存在の定義はよくわからないが<br>\nある意味，すべての生命は霊的だと言えなくもない。<br>\n実体と思っているものは流れの中のある時点の点<br>\nあるいは流れそのものであるからである。  </p>\n<p>個と捉えているものは流れであり<br>\nより大きな流れの中の一部でもある。  </p>\n<p>少なくとも構成要素は<br>\nそのもののアイデンティティとは無関係である。  </p>\n<br>\n<p>“ゆく河の流れは絶えずして，しかももとの水にあらず”  </p>\n<br>\n<p>水という構成要素は刻々と流れていき<br>\nある時点の水であることがその川をその川たらしめているわけではないが<br>\n「流れている」ということは川であることの条件の一部である。  </p>\n<br>\n<p>* * * * * * * * * *</p>\n<br>\n<p>それぞれの事物，あるいは自分というものが<br>\n離散的であるとか流動的であるとかいうように捉えてしまうと<br>\n建設的に思考を重ねることが困難になってしまう。  </p>\n<p>昨日の世界と今日の世界は違うし，昨日の自分と今日の自分も違う。  </p>\n<p>そのように考えてしまうと<br>\n昨日はそれでよかったことが今日はそうでないかもしれず<br>\nまた一から検証し，学びなおさなければならなくなる。  </p>\n<p>それでは厄介なので<br>\n同一のものみなし，過去の知見を今日でも活かせるとした方が<br>\n建設的に思考を成長させられると考えるのも無理もない。<br>\n実際に，一定の部分は適用可能であろう。  </p>\n<p>しかし，適用できない部分もある。<br>\n過去に適用できたと思っていたことが<br>\n新たな発見によって実は間違いであったと気づくこともあるだろう。<br>\nまた，世界のありようが変わったため<br>\nもはや適用できなくなったということもあるだろう。  </p>\n<br>\n<p>それでも今までは適用できたと思って現実を受け入れられないのは<br>\nいわばパラダイムの慣性の法則のようなものだ。  </p>\n<p>人類の知見はとても大きなものでこれからも益々発展するだろう。<br>\nしかしそれは同時に過去に発見されたものでもある。<br>\n最新の発見も次の瞬間は過去になるのである。<br>\nむしろ現代のパラダイムに則っているほど人々に与える慣性は強力である。  </p>\n<p>今までにどんなに多くのことを学んできたとしても，<br>\n常に「今」自分が感じていることから学ぶということが大切である。  </p>",
